// 定义模块count配置对象
export default{
    // 开启命名空间-不然辅助函数定位不到
    namespaced:true,
    // 4个关键字段
    state:{
        countnum:0,
        name:'vuex-state-name'
    },
    mutations:{
        jia(state,value){
                state.countnum+=value
        },
        jian(state,value){
            state.countnum-=value
         }
    },
    actions:{
        jian(context,value){
            console.log(context);
            context.commit('jian',value)
        },
        waitjia(context,value){
            console.log('value',value);
            setTimeout(() => {
                context.commit('jia',value)
            }, 1000);
        }
    },
    getters:{
        bignum(state){
           return state.countnum+100
        },
        funystring(state){
            return '这个字符串来自于store.getters'
        
    }
    }
}