// 引入vue
import Vue from 'vue';
// 引入vuex库
import Vuex from 'vuex'
// 引入vuex的辅助函数

// 导入模块count
import count from './count'

// 在vuex.store初始化之前就要向全局Vue注册Vuex
Vue.use(Vuex)


// 使用Vuex的Store构造函数创建store-并传入配置对象
export default new Vuex.Store({
    // modules字段引入模块
    modules:{
        count
    }
})

// 最后去引入main.js引入这个store对象并注入即可