<template>
<div>
    <h3>Message组件</h3>
  <ul>
     <li v-for="i in allmessage" :key="i.id">
         <!-- <router-link
         :to="{
             name:'Messagedetail',
             query:i,
             params:i
         }"
         >
         {{i.title}}
         </router-link> -->
         <span>{{i.title}}</span>
         <div><button @click="pushshow(i)">push导航</button><button @click="replaceshow(i)">replace导航</button></div>
     </li>
     <router-view></router-view>
  </ul>

</div>
</template>

<script>
export default {
    name:'Message',
   data() {
       return {
            allmessage:[
        {id:'01',
        title:'第1条信息'
        },
        {id:'02',
        title:'第2条信息'
        },
        {id:'03',
        title:'第3条信息'
        },
        {id:'04',
        title:'第4条信息'
        },
        {id:'05',
        title:'第5条信息'
        },
    ]
       }
   },
   methods:{
       pushshow(i){
          this.$router.push({
             name:'Messagedetail',
             query:i,
             params:i
          })
       },
       replaceshow(i){
        this.$router.replace({
             name:'Messagedetail',
             query:i,
             params:i
        })
       },
   }

}
</script>

