import Stuone from "../pages/Stuone.vue";
import Stutwo from "../pages/Stutwo.vue";
import News from '../pages/News.vue'
import Message from '../pages/Message.vue'
import Messagedetail from '../pages/Messagedetail.vue'

import router from 'vue-router'

import Vue from 'vue';
Vue.use(router)

const Router =new router({
    routes:[
        {
            name:'stu1',
            path:'/stu1/:name',
            component:Stuone,
            meta:{
                title:'stu1'
            }
        },
        {

            path:'/stu2',
            component:Stutwo,
            meta:{
                title:'stu2'
            },
            children:[
                // 二级路由即children中的path不允许带斜杠
                {
                    name:'xinwen',
                    path:"News",
                    component:News,
                    meta:{
                        needtoken:true,
                        title:'News'
                    },
                    // beforeEnter(to,from,next){
                    //   ......
                    // }
                },
                // 二级路由即children中的path不允许带斜杠
                {
                    name:'Message',
                    path:"Message",
                    component:Message,
                    meta:{
                        needtoken:true,
                        title:"Message"
                    },
                    children:[
                        {
                            name:"Messagedetail",
                            path:'Messagedetail',
                            component:Messagedetail,
                            props(route){
                                return {
                                    id:route.query.id,
                                    title:route.query.title
                                }
                            },
                            meta:{
                                title:'Messagedetail'
                            }
                        }
                    ]
                }

            ]

        }
        ]
})

// 注册全局守卫api 三个参数分别为目标路径 来源路径 放行函数  
Router.beforeEach((to, from, next) => {
  if(to.meta.needtoken){
      if(localStorage.getItem('token')==='token-false'){
        next()
      }else{
        alert('token令牌不正确 请登录!')
      }
      
  }else{
    next();
  }
})

// 导航成功进入/放行后执行该函数 既然已经放行就没有next参数
Router.afterEach((to,from)=>{
document.title=to.meta.title;
})



export default Router

